import Vue from 'vue'
import Router from 'vue-router'
import page1 from '@/components/Page1'
import page2 from '@/components/Page2'
import page3 from '@/components/Page3'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: page1
    },
    {
      path: '/page2',
      component: page2
    },
    {
      path: '/page3',
      component: page3
    }
  ]
})